export default {
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
		 
			fanList:['ddd'],
			seclist:['全部','有效','无效'],
			scur:10,
			sectshow:true,
		}
	},
	methods: {
		// 返回上一页
		goback:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		scrclick:function(){
			var that=this
			that.sectshow=!that.sectshow
		},
		// 粉丝筛选
		sectnav:function(e){
			var that=this
			var scur=e.currentTarget.dataset.scur
			that.scur = scur
			that.sectshow=true
		},
		 
	}
}
